* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  color: #d3d3d3;
  background: linear-gradient(45deg, #061de3, #e306ca);
  gap: 40px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 1200px;
  height: 500px;
  perspective: 800px;
  justify-content: center;
  align-items: center;
  gap: 100px;
  margin: 50px auto 0;
  list-style: none;
  display: flex;
}

.card:hover {
  cursor: pointer;
  transform: rotateY(180deg);
}

.card {
  height: 100%;
  width: 100%;
  transform-style: preserve-3d;
  transition: transform 1.5s;
  position: relative;
}

.front, .back {
  height: 100%;
  width: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  color: #fff;
  border-radius: 2rem;
  text-decoration: none;
  display: block;
  position: absolute;
  box-shadow: 0 0 5px 2px rgba(50, 50, 50, .25);
}

.front {
  background-position: center;
  background-size: cover;
}

.front-img-1 {
  background-image: url("gallary-neon-img.1eb75325.jpg");
}

.front-img-2 {
  background-image: url("video-img.44545537.jpg");
}

.front-img-3 {
  background-image: url("feedback-img.24bf3e31.jpg");
}

.back {
  background-color: #3a3a3a;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
  display: flex;
  transform: rotateY(180deg);
}

